#app {
  display: grid;
  grid-template-areas: "header header" "aside main" "aside footer";
  grid-template-columns: minmax(200px, 264px) auto;
  grid-template-rows: auto auto auto;
  overflow: hidden;
}
::-webkit-scrollbar-thumb {
  width: 8px;
  height: 30px;
  background: #000;
  border-radius: 10px;
  background-color: #060028;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd8a;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #060028;
}
.main {
  grid-area: main;
  min-height: 100vh;
  position: relative;
}
.form-label {
  font-size: 0;
}
.br {
  border-right: 1px solid #fff;
}
@media (max-width: 992px) {
  #app {
    grid-template-areas: "header" "main" "footer";
    grid-template-columns: 100%;
    grid-template-rows: auto auto auto;
    position: relative;
  }
  .aside {
    position: absolute;
    top: 74px;
    left: 0;
    padding: 10px 20px;
    z-index: 35;
    transform: translateX(-100%);
    transition: all 0.6s ease-in;
    background: #fff;
  }
  .naw-show {
    transform: translateX(0);
  }
}
.toast-btn {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
  background-color: transparent;
  border: 0;
}
.toast-text {
  width: 246px;
}
@media (max-width: 368px) {
  .toast-text {
    width: 230px;
  }
}
.logo-link {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease-out;
}
.logo-link:hover {
  color: #ffffff58;
  text-decoration: none;
}
.header {
  grid-area: header;
  padding: 16px 20px;
  color: #fff;
  background: #150E41;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  position: relative;
}
.header .nav-item .nav-link {
  color: #fff;
}
.burger {
  display: none;
}
.profile-linck {
  display: none;
}
@media (max-width: 992px) {
  .burger {
    display: block;
    position: absolute;
    z-index: 10;
    top: -14px;
    right: 16px;
  }
  .profile-linck {
    display: block;
    color: #fff;
    margin-right: 70px;
  }
  .header-bignav {
    display: none;
  }
}
.navbar-heading {
  font-size: 0.75rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  padding: 0.5rem 1rem;
  color: #8898aa;
}
.menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
  width: 60px;
  height: 60px;
}
.line {
  fill: none;
  stroke: #fff;
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}
.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}
.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.header .nav .nav-link span {
  margin-right: 4px;
}
@media (max-width: 472px) {
  .header .nav .nav-link {
    padding: 0.3rem 0.5rem;
    font-size: 14px;
  }
  .header .nav .nav-link span {
    display: none;
  }
  .burger {
    transform: scale(85%);
    top: -10px;
    right: 12px;
  }
}
.aside {
  grid-area: aside;
  box-shadow: 26px 9px 10px -8px rgba(0, 0, 0, 0.1);
  background: #060028;
}
.aside a {
  color: #fff;
}
.aside .nav .nav-link span {
  color: #fff;
  margin-left: 4px;
}
.aside .nav .nav-link span:hover,
.aside a:hover,
.aside a:active {
  transition: all 0.4s ease-out;
  color: #ffffff82;
}
.aside .nav .nav-link span:hover,
.aside a:active .aside .nav .nav-link span {
  border-bottom: 1px solid #ffffff82;
}
.balance__card {
  transition: all 0.3s ease-in;
}
.balance__card:hover {
  cursor: pointer;
}
.balance__card a {
  color: #343a40;
}
.balance__card.active {
  color: #fff;
  background-color: #343a40;
  border-color: #454d55;
}
.balance__card.active a {
  color: #fff;
}
.substrate {
  border-radius: 6px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.balance .alert {
  text-transform: uppercase;
  color: #343a40;
  font-size: 0.75rem;
}
.main-alerts .pagination a {
  color: #343a40;
}
.home-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home-nav__btn {
  min-width: 108px;
}
@media (max-width: 768px) {
  .home-nav {
    flex-direction: column;
    align-items: flex-start;
  }
  .home-nav__btn {
    margin-top: 15px;
  }
}
.modal-btn {
  width: 144px;
}
@media (max-width: 576px) {
  .modal-btn {
    width: 210px;
  }
  .btn-wrap {
    flex-direction: column;
    align-items: center;
  }
  .transactions {
    overflow-x: scroll;
  }
}
.video {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.voice-btn {
  width: 60px;
}
@media (max-width: 576px) {
  .investment-table {
    overflow-x: scroll;
  }
  .investment-table .btn {
    min-width: 94px;
  }
}
.footer {
  grid-area: footer;
  padding: 25px 20px 20px;
  margin-top: 30px;
}
